"use client";

import { useState } from "react";
import { toast } from "sonner";
import styles from "./register.module.css"; // CSS Module

export default function RegisterPage() {
  const [user, setUser] = useState({ username: "", email: "", password: "" });
  const [isLoading, setIsLoading] = useState(false);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setIsLoading(true);
    try {
      const res = await fetch("http://localhost:4000/api/auth/register", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(user),
      });
      const data = await res.json();
      if (res.ok) {
        toast.success("Inscription r√©ussie !");
        window.location.href = "/login";
      } else {
        toast.error(data.error || "Erreur inscription");
      }
    } catch {
      toast.error("Erreur serveur");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className={styles.container}>
      {/* Partie gauche */}
      <div className={styles["left-section"]}>
        <img src="/doctors.png" alt="Doctors" className={styles["doctors-image"]} />
      </div>

      {/* Partie droite */}
      <div className={styles["right-section"]}>
        <div className={styles["login-box"]}>
          <form onSubmit={handleSubmit}>
            <div className={styles["input-group"]}>
              <label>Username</label>
              <input type="text" name="username" value={user.username} onChange={handleChange} required />
            </div>

            <div className={styles["input-group"]}>
              <label>Email</label>
              <input type="email" name="email" value={user.email} onChange={handleChange} required />
            </div>

            <div className={styles["input-group"]}>
              <label>Password</label>
              <div className={styles["password-wrapper"]}>
                <input type="password" name="password" value={user.password} onChange={handleChange} required />
                <span className={styles["eye-icon"]}>üëÅÔ∏è</span>
              </div>
            </div>

            <button type="submit" className={styles.register} disabled={isLoading}>
              {isLoading ? "Cr√©ation..." : "Register"}
            </button>

            <div className={styles["google-button-container"]}>
              <button type="button" className={styles["register-button"]}>
                <img src="/Google.png" alt="Google Logo" /> Create account with Google
              </button>
            </div>

            <p className={styles["register-text"]}>
              Already have an account? <a href="/login">Login</a>
            </p>
          </form>
        </div>
      </div>
    </div>
  );
}
